import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import foto from './foto_portada.jpg';

function Home(props) {
  return (
    <div>
      <div className='row portada'>
        <PhotoHome />
        <Title />
      </div>
      <div className='row'>
        <Menu />
      </div>
      <div className='row'>
        <Panel />
      </div>
    </div>
  );
}
function PhotoHome(props) {
  return (
    <div className='column-photo-home'>
      <div className='photo-home'>
        <img src={foto} />
      </div>
    </div>);
}

function Title(props) {
  return (
    <div className='column-title-home'>
      <div className='title-home'>
        <h1>Hola, soy Enderson Gonzalo Diaz Muñoz</h1>
      </div>
    </div>);
}

function Menu(props) {
  return (

    <div className='column-menu-home'>
      <div className='menu-home'>
        <ul>
          <li>Sobre mi</li>
          <li>Proyectos</li>
          <li>Conocimientos</li>
          <li>Contáctame</li>
        </ul>
      </div>
    </div>
  )
}

